<template>
    <div class="slider-cont">
        <div class="divs">
            <ul class="leftSlide">
                <li class="slideLis-LOGO"></li>
                <li class="slideLis-homePage"></li>
                <li class="slideLis-msg"></li>
            </ul>
            <el-collapse
                v-model="activeName"
                accordion
                class="leftSlide"
                @change="handleChange"
            >
                <el-collapse-item
                    class="slideLis-msg"
                    title=""
                    name="2"
                    v-bind:class="{color60: !isshow,color30: isshow}"
                    @click.native="isshow = !isshow;"
                >
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>

                </el-collapse-item>
                <el-collapse-item
                    title=""
                    name="3"
                >
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                </el-collapse-item>
                <el-collapse-item
                    class="slideLis-msg"
                    title=""
                    name="4"
                >
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                </el-collapse-item>
                <el-collapse-item
                    class="slideLis-msg"
                    title=""
                    name="5"
                >
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                </el-collapse-item>
                <el-collapse-item
                    class="slideLis-msg"
                    title=""
                    name="6"
                >
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                </el-collapse-item>
                <el-collapse-item
                    class="slideLis-msg"
                    title=""
                    name="7"
                >
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                    <div class="sliderChildrens"></div>
                </el-collapse-item>
            </el-collapse>
        </div>

    </div>
</template>

<script>
export default {
    methods: {
        handleNodeClick(data) {
            console.log(data);
        },
        handleChange(val) {
            console.log(val);
        },
        collapseClick(e) {
            console.log(e.target);
        }
    },
    data() {
        return {
            activeName: "",
            isshow: true
        };
    }
   
};
</script>
<style lang="less" scoped>
@import "../../../static/css/init.css";
.slider-cont {
    /* display: flex; */
    height: 100%;
}
.divs {
    width: 19.5%;
    float: left;
    background: #fff;
    height: 100%;
}
.leftSlide {
    // height: 100%;
    .slideLis-LOGO {
        height: 104px;
    }
    .slideLis-homePage {
        min-height: 48px;
        font-size: 13px;
        color: #303030;
        letter-spacing: -0.27px;
        line-height: 48px;
    }
    .slideLis-msg {
        text-align: center;
        min-height: 48px;
        line-height: 48px;
        margin-bottom: 8px;
        font-size: 12px;
        // color: #606060;
        letter-spacing: -0.25px;
        cursor: pointer;
        // background: #000;
    }
    .sliderChildrens {
        height: 40px;
        line-height: 40px;
    }
}
.color60 {
    color: red !important;
}
.color30 {
    color: blue !important;
}
.el-icon-arrow-right:before {
    content: "" !important;
}
.el-collapse-item__header {
    display: block;
    text-align: center;
    font-size: 12px;
    // color: #606060;
}
</style>


